@media (max-width: 767px) {
	.menu {
		position: fixed;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 0;
		right: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		background-color: rgba(0, 0, 0, .8);
		transition: 0.9s all;
		&_active {
			right: 0; }
		&_item {
			padding-left: 0;
			border: none;
			margin-top: 15px; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 25px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #fff;
			margin-bottom: 2px;
			transition: -0.9s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
	.subheader {
		&_logo {
			img {
				width: 100%; } }
		&_phone {
			display: block;
			font-style: normal;
			margin-top: 10px;
			font-weight: 400;
			font-size: 18px;
			text-transform: uppercase;
			color: #FFFFFF;
			text-align: center; }
		&_btn {
			display: block;
			height: 40px;
			width: 200px;
			margin: 0 auto;
			margin-top: 15px; } }
	.promo {
		&_header {
			font-size: 33px; }
		&_subheader {
			font-size: 21px; } }
	.reasons {
		.title {
			font-size: 25px; } }
	.choice {
		&_img {
			width: 510px; }
		&_prices {
			width: 535px;
			border-radius: 20px; } }
	footer {
		padding: 25px 0 20px 0; }
	.footer {
		&_divider {
			width: 253px;
			margin: 0 auto; }
		&_wrapper {
			padding-top: 22px;
			display: flex;
			flex-wrap: wrap;
			height: 40vh;
			flex-direction: column; }
		&_wrapper > div {
			margin: 0 auto;
			&:nth-child(1) {
				order: 2;
				margin: 0 auto;
				margin-right: 80px; }
			&:nth-child(2) {
				order: 1; }
			&:nth-child(3) {
				order: 3;
				margin: 0 auto; } }
		&_links {
			width: 315px;
			&_main {
				padding: 0;
				font-size: 14px; }
			&_sub {
				flex-wrap: wrap;
				justify-content: center;
				a {
					margin: 0 7px; } }
			&_lang {
				margin-top: 10px; } } } }
